.slider-container {
  position: relative;
  width: 720px;
}

.slider-slides {
  display: flex;
  flex-direction: row;
  z-index: 1;
  height: 100%;
}

.slider-btn {
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;
  background-color: var(--darkblue);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  cursor: pointer;
  border-radius: 50%;
}

.arrow-left {
  left: 20px;
}

.arrow-right {
  right: 20px;
}

/* Arrow styles */
.arrow-left i, .arrow-right i {
  border: solid var(--grey);
  border-width: 0 3px 3px 0;
  padding: 6px;
  position: relative;
}

.arrow-left i {
  transform: rotate(135deg);
  left: 2px;
}

.arrow-right i {
  transform: rotate(-45deg);
  right: 2px;
}

.slider-nav {
  display: flex;
  gap: 5px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.slider-nav-item {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--darkblue);
  cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/modules/imageSlider.css"],"names":[],"mappings":"AAAA,eAAe;AACf;EACE,kBAAkB;EAClB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,UAAU;EACV,YAAY;AACd;;AAEA;EACE,aAAa;EACb,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,iCAAiC;EACjC,kBAAkB;EAClB,QAAQ;EACR,2BAA2B;EAC3B,UAAU;EACV,eAAe;EACf,kBAAkB;AACpB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,WAAW;AACb;;AAEA,iBAAiB;AACjB;EACE,yBAAyB;EACzB,yBAAyB;EACzB,YAAY;EACZ,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,SAAS;AACX;;AAEA;EACE,yBAAyB;EACzB,UAAU;AACZ;;AAEA;EACE,aAAa;EACb,QAAQ;EACR,kBAAkB;EAClB,YAAY;EACZ,SAAS;EACT,2BAA2B;AAC7B;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,iCAAiC;EACjC,eAAe;AACjB","sourcesContent":["/* Slider CSS */\n.slider-container {\n  position: relative;\n  width: 720px;\n}\n\n.slider-slides {\n  display: flex;\n  flex-direction: row;\n  z-index: 1;\n  height: 100%;\n}\n\n.slider-btn {\n  display: flex;\n  padding: 10px;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--darkblue);\n  position: absolute;\n  top: 50%;\n  transform: translateY(-50%);\n  z-index: 2;\n  cursor: pointer;\n  border-radius: 50%;\n}\n\n.arrow-left {\n  left: 20px;\n}\n\n.arrow-right {\n  right: 20px;\n}\n\n/* Arrow styles */\n.arrow-left i, .arrow-right i {\n  border: solid var(--grey);\n  border-width: 0 3px 3px 0;\n  padding: 6px;\n  position: relative;\n}\n\n.arrow-left i {\n  transform: rotate(135deg);\n  left: 2px;\n}\n\n.arrow-right i {\n  transform: rotate(-45deg);\n  right: 2px;\n}\n\n.slider-nav {\n  display: flex;\n  gap: 5px;\n  position: absolute;\n  bottom: 20px;\n  left: 50%;\n  transform: translateX(-50%);\n}\n\n.slider-nav-item {\n  width: 10px;\n  height: 10px;\n  border-radius: 50%;\n  background-color: var(--darkblue);\n  cursor: pointer;\n}"],"sourceRoot":""}]);
